.About{
    z-index: 1000000;
}
.seller-title{
    display: none;
}
.seller-text{
    display: none;
}

.header {
    //height: 15vh;
    background: #c8e8fc;
    padding: 1vw;
    span {
        margin: 5vw;
    }
    .user-name {
        display: inline-block;
        margin-top: 2vw;
        font-size: 1.8em;
        position: relative;
        top: 3vh;

    }
}
.user-info{
    text-align: center;

    padding: 5vw;

    //box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);
    .coin{
        width: 40vw;
        border: 1px solid;
        margin: auto;
        font-size: 1.25em;
        font-weight: 700;
        color: #fa8163;
        padding: 2vw;
    }
}
.nav-route {
    display: flex;
    justify-content: center;
    align-items: center;
    // padding: 5vw;
    width: 100%;
    //box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);
    a {
        text-align: center;
        color: gray;
        margin: 5px;
        font-size: 15px;
        div{
            color: black;
        }
    }
    .active {
        color: #71ceff;
        border-bottom: 3px solid #71ceff;
        div{
            color: black;
        }
    }
}
.routes-container{
    background-color: #f6f6f6;
}
